import React from 'react';
import Autosuggest from 'react-autosuggest';
import Slider from 'react-rangeslider'
import ReactGA from 'react-ga';

import logo from './logo.svg';
import 'react-rangeslider/lib/index.css'
import './App.css';

ReactGA.initialize("UA-166956768-1")

function escapeRegexCharacters(str) {
  return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}

function getSuggestionValue(suggestion) {
  return suggestion.title;
}

function renderSuggestion(suggestion) {
  return (
    <span>{suggestion.title} ({suggestion.year})</span>
  );
}

function getRecommendations(all_titles, items, n, weight) {
  if (all_titles === undefined) {
    return [];
  }
  const indices = items.map((item) => item.index);
  if (indices.length == 0) {
    return [];
  }
  var tag_vec = Array.from(new Set([].concat.apply([], items.map(item => item.tags_enc))));
  var genre_vec = Array.from(new Set([].concat.apply([], items.map(item => item.genres_enc))));
  var nconst_vec = Array.from(new Set([].concat.apply([], items.map(item => item.nconst_enc))));

  var N = all_titles.length;
  var scored = Array(N);
  for (var i = 0; i < N; i++) {
    var item = all_titles[i];
    var tag_v = item.tags_enc;
    var tag_both = tag_vec.filter(value => tag_v.includes(value));
    var genre_v = item.genres_enc;
    var genre_both = genre_vec.filter(value => genre_v.includes(value));
    var nconst_v = item.nconst_enc;
    var nconst_both = nconst_vec.filter(value => nconst_v.includes(value));
    var score = 0;
    if (!indices.includes(i)) {
      score = weight[0] * tag_both.length
            + weight[1] * genre_both.length
            + weight[2] * nconst_both.length;
    }
    scored[i] = {
      item: item,
      score: score,
      why_tag: tag_both,
      why_genre: genre_both,
      why_nconst: nconst_both,
    };
  }
  scored = scored.sort((a, b) => b.score - a.score);
  var out = scored.slice(0, n);
  return out;
}


class TitleInput extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      value: '',
      suggestions: []
    };    
  }

  onChange = (event, { newValue, method }) => {
    this.setState({
      value: newValue
    });
  };

  onSuggestionsFetchRequested = ({ value }) => {
    this.setState({
      suggestions: this.props.getSuggestions(value)
    });
  };

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: []
    });
  };

  onSuggestionSelected = (event, { suggestion, suggestionValue, suggestionIndex, sectionIndex, method }) => {
    this.props.onSelected(suggestion);
    this.setState({value: ""})
  }

  render() {
    const { value, suggestions } = this.state;
    const inputProps = {
      placeholder: "Enter a movie title.",
      value,
      onChange: this.onChange
    };

    return (
      <Autosuggest 
        suggestions={suggestions}
        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
        onSuggestionsClearRequested={this.onSuggestionsClearRequested}
        getSuggestionValue={getSuggestionValue}
        renderSuggestion={renderSuggestion}
        inputProps={inputProps}
        onSuggestionSelected={this.onSuggestionSelected}
      />
    );
  }
}


class WeightInput extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.props.onChange(e.target.value);
  }

  render() {
    return (
      <input
        type="number" min="0" max="1" step="0.1"
        value={this.props.value}
        onChange={this.handleChange}
      ></input>
    )
  }
}


class HSlider extends React.Component {
  constructor(props, context) {
    super(props, context)
    console.log(this.props.onChangeCB)
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(value) {
    this.props.onChangeCB(value);
  }

  render() {
    return (
      <Slider
        value={this.props.value}
        orientation="horizontal"
        onChange={this.handleChange}
      />
    )
  }
}

class WeightSlider extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="weight">
        <HSlider onChangeCB={this.props.onChangeCB} value={this.props.value} />
      </div>
    )
  }
}


class RecList extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const recs = getRecommendations(this.props.all_titles,
                                    this.props.items,
                                    this.props.nrecs,
                                    this.props.weight);
    var rec_items = recs.map((item) => {
      var x = item;
      var score = item.score;
      var item = item.item;
      var oc = () => this.props.addItem(item);
      var url = "https://www.imdb.com/title/" + item.tconst + "/"
      return (
        <li className="title" key={score + item.index + 1000}>
          <button className="plus" onClick={oc}> + </button>
          <div className="tooltip">
            <a href={url} target="_blank" rel="noopener noreferrer">
              ({item.year}) {item.title}
            </a>
            <div className="tooltiptext">
              <h4>Shared People:</h4>
              <p>{x.why_nconst.map(i => this.props.metadata.nconsts[i]).join(", ")}</p>
              <h4>Shared Tags:</h4>
              <p>{x.why_tag.map(i => this.props.metadata.tags[i]).join(", ")}</p>
              <h4>Shared Genres:</h4>
              <p>{x.why_genre.map(i => this.props.metadata.genres[i]).join(", ")}</p>
              <hr/>
              <p>{url}</p>
            </div>
          </div>
        </li>
      );
    })

    return (
      <div>
        <h2>What you might like:</h2>
        <ul className="results">
          {rec_items}
        </ul>
      </div>
    )
  }
}


class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: Array(0),
      weight: [100, 100, 100],
      nrecs: 15,
    };
  }

  getSuggestions(self, value) {
    const escapedValue = escapeRegexCharacters(value.trim());
    
    if (escapedValue === '') {
      return [];
    }

    const regex = new RegExp('^' + escapedValue, 'i');

    const matches = self.props.all_titles.filter(item => regex.test(item.title));
    const indices = self.state.items.map((item) => item.index)
    return matches.filter(item => indices.indexOf(item.index) < 0);
  }

  handleSelected = (item) => {
    this.setState(state => {
      const items = state.items.concat(item);
      return {...state, items: items};
    })
  }

  removeItem = (index) => {
    console.log("removing: " + index);
    this.setState(state => {
      const items = state.items.filter(item => item.index != index);
      return {...state, items: items};
    })
  }

  changeWeight = (index, value) => {
    this.setState(state => {
      var weight = state.weight;
      weight[index] = value;
      return {...state, weight: weight};
    })
  }

  render() {

    var title_items = this.state.items.map((item) => {
      var oc = () => this.removeItem(item.index);
      var url = "https://www.imdb.com/title/" + item.tconst + "/"
      return (
        <li className="title" key={item.index}>
          <button className="minus" onClick={oc}> x </button>
          <div className="tooltip">
            <a href={url} target="_blank" rel="noopener noreferrer">
              ({item.year}) {item.title}
            </a>
            <div className="tooltiptext">
              <h4>People:</h4>
              <p>{item.nconst_enc.map(i => this.props.metadata.nconsts[i]).join(", ")}</p>
              <h4>Tags:</h4>
              <p>{item.tags_enc.map(i => this.props.metadata.tags[i]).join(", ")}</p>
              <h4>Genres:</h4>
              <p>{item.genres_enc.map(i => this.props.metadata.genres[i]).join(", ")}</p>
              <hr/>
              <p>{url}</p>
            </div>
          </div>
        </li>
      )
    })
    
    return (
      <div>
        <div className="ui_section">
          <h2>What do you like?</h2>
          <TitleInput id="title"
            onSelected={(value) => this.handleSelected(value)}
            getSuggestions={(value) => this.getSuggestions(this, value)}
          />
          <ul>
            {title_items}
          </ul>
          <h2>How do you want it?</h2>
          <div className="weight">People</div>
          <div className="weight">Tags</div>
          <div className="weight">Genres</div>
          <br />
          <WeightSlider
            onChangeCB={value => this.changeWeight(2, value)}
            value={this.state.weight[2]} />
          <WeightSlider
            onChangeCB={value => this.changeWeight(0, value)}
            value={this.state.weight[0]} />
          <WeightSlider
            onChangeCB={value => this.changeWeight(1, value)}
            value={this.state.weight[1]} />
        </div>
        <div className="ui_section">
          <RecList
            all_titles={this.props.all_titles}
            items={this.state.items}
            nrecs={this.state.nrecs}
            weight={this.state.weight}
            addItem={(item) => this.handleSelected(item)}
            metadata={this.props.metadata}
          />
        </div>
      </div>
    )
  }
}


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      all_titles: Array(0),
      metadata: {},
    }
  }
  componentDidMount() {
    fetch('titles.json')
      .then(titles => titles.json())
      .then((titles) => {
        fetch('metadata.json')
        .then(metadata => metadata.json())
        .then(metadata => this.setState({metadata: metadata, all_titles: titles}))
      })
      .catch(err => { throw err });
  }
  render() {
    return (
      <Main all_titles={this.state.all_titles} metadata={this.state.metadata} />
    )
  }
}

export default App;
